import React from 'react';
import PropTypes from 'prop-types';

export default function Form({ onSubmit, currentUser, analitics }) {
  return (
    <form onSubmit={onSubmit}>
      <fieldset id="fieldset">
        <p>Sign into log analitics, { currentUser.accountId }!</p>
        <p className="highlight">
          <label htmlFor="user_name">User name</label>
          <input
            autoComplete="off"
            autoFocus
            id="user_name"
            value="{ analitics.user_name }"
            required
          />
        </p>
        <p>
          <label htmlFor="app_id">App #</label>
          <input
            autoComplete="off"
            autoFocus
            id="app_id"
            value="AnaliticsApp"
            required
          />
        </p>
        <p>
          <label htmlFor="action_id">Action #</label>
          <input
            autoComplete="off"
            autoFocus
            id="action_id"
            value="AnaliticsAction"
            required
          />
        </p>
        <button type="submit">
          Submit
        </button>
      </fieldset>
    </form>
  );
}

Form.propTypes = {
  onSubmit: PropTypes.func.isRequired,
  currentUser: PropTypes.shape({
    accountId: PropTypes.string.isRequired,
    balance: PropTypes.string.isRequired
  })
};
